"use client"

import { motion } from "framer-motion"
import type { WeddingSettings } from "@/lib/types"
import { Calendar, Clock, MapPin, Flower2 } from "lucide-react"
import { getEmbedUrl, getDirectionUrl } from "@/lib/map-utils"

interface EventSectionProps {
  settings: WeddingSettings
}

export function EventSection({ settings }: EventSectionProps) {
  const weddingDate = new Date(settings.wedding_date)
  const formattedDate = weddingDate.toLocaleDateString("id-ID", {
    weekday: "long",
    year: "numeric",
    month: "long",
    day: "numeric",
  })
  const title = settings.section_title_event || "Wedding Event"

  return (
    <section className="py-20 px-6">
      <div className="max-w-4xl mx-auto">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          className="text-center mb-12"
        >
          <h2 className="font-serif text-3xl md:text-4xl text-foreground">{title}</h2>
        </motion.div>

        <div className="max-w-xl mx-auto">
          {/* Resepsi */}
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            className="glass rounded-3xl p-8 text-center"
          >
            <div className="w-16 h-16 mx-auto mb-4 rounded-full bg-gold/10 flex items-center justify-center">
              <Flower2 className="w-6 h-6 text-gold" />
            </div>
            <h3 className="font-serif text-2xl text-foreground mb-6">Resepsi</h3>

            <div className="space-y-4">
              <div className="flex items-center justify-center gap-3 text-muted-foreground">
                <Calendar className="w-5 h-5 text-gold" />
                <span>{formattedDate}</span>
              </div>
              <div className="flex items-center justify-center gap-3 text-muted-foreground">
                <Clock className="w-5 h-5 text-gold" />
                <span>{settings.reception_time} - {settings.reception_time_end} WIB</span>
              </div>
              <div className="flex items-start justify-center gap-3 text-muted-foreground">
                <MapPin className="w-5 h-5 text-gold flex-shrink-0 mt-0.5" />
                <div className="text-left">
                  <p className="font-medium text-foreground">{settings.reception_location}</p>
                  <p className="text-sm">{settings.reception_address}</p>
                </div>
              </div>
            </div>
          </motion.div>
        </div>

        {/* Google Maps */}
        {settings.venue_lat && settings.venue_lng && (
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          className="mt-8 glass rounded-3xl overflow-hidden"
        >
          <iframe
            src={getEmbedUrl(settings.venue_lat, settings.venue_lng)}
            width="100%"
            height="300"
            style={{ border: 0 }}
            allowFullScreen
            loading="lazy"
            referrerPolicy="no-referrer-when-downgrade"
            title="Lokasi Venue"
          />
          <div className="p-4 text-center">
            <a
              href={getDirectionUrl(settings.venue_lat, settings.venue_lng)}
              target="_blank"
              rel="noopener noreferrer"
              className="inline-flex items-center gap-2 px-6 py-3 glass-gold rounded-full text-gold font-medium hover:opacity-90 transition-opacity"
            >
              <MapPin className="w-5 h-5" />
              Buka di Google Maps
            </a>
          </div>
        </motion.div>
        )}
      </div>
    </section>
  )
}
